import { navigate } from "../components/Router";
import "../stylesheets/pages/home.scss";
export default function Home(props) {
    return <div className="home page">
        <div className="cover">
            <h2>Welkom</h2>
            <h1>Thijn IT</h1>
        </div>
        <div className="card">
            <h3>over Thijn IT</h3>
            <p>
                Mijn naam is Thijn van den Bergh. Ik ben student aan Fontys ICT in Eindhoven. Zoekt u een software ontwikkerlaar voor uw project of website? Wellicht kan ik u helpen!
            </p>
        </div>
        <div className="card">
            <h3>Vaardigheden</h3>
            <p>
                Klik <a onClick={()=>navigate("/vaardigheden")}>hier</a> om mijn vaardigheden te bekijken.
            </p>
        </div>
    </div>
}